import React, { Component } from 'react'
import NavBar from './NavBar';
import {connect} from 'react-redux';
import {deleteproduct,gettotal} from './Action';
import {Button,Card,ListGroup} from 'react-bootstrap';
import { Link } from 'react-router-dom';
import {Helmet} from 'react-helmet'  
class Cart extends Component {
  constructor(props) {
    super(props)
  
    this.state = {
       total:0
    }
  }

  render() {
       var total1=0
       for (let i=0; i<this.props.products.length;i++)
       {
          total1=total1+this.props.products[i].product_totalprice
       }
       this.state.total=total1
       this.props.gtotal(total1)
       console.log(this.props.products)
    return (
      <div>
            <Helmet>                                
            <style>{'body { background-image:url("https://images.unsplash.com/photo-1470790376778-a9fbc86d70e2?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1898&q=80"); }'}</style>
        </Helmet>
 
            <NavBar />
           <h1>Item Details</h1>

<ListGroup horizontal style={{display:'flex',flexWrap:'wrap'}}>
           {this.props.products.map((veg,index)=>
              <ListGroup.Item key={index}>
                 <Card style={{ width: '18rem' }} >
{index==0?<img className="img" src={"data:image/jpeg;base64,"}/>:null}
                 {index==1?<img  className="img" src={"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTz0SscjInwEDMS2A1bSXSp8ZueXHYbJowtdoIAV6l60aXko9EKxRP41sLFc3U6qM4g4oo&usqp=CAU"}/>:null}
                 {index==2?<img  className="img" src={"https://thumbs.dreamstime.com/b/lady-finger-okra-vegetables-white-cup-food-green-fresh-organic-closeup-isolated-background-133846727.jpg"}/>:null}
                 {index==3?<img  className="img" src={"https://freshomart.in/wp-content/uploads/2020/08/100908967-eggplant-isolated-on-white.jpg"}/>:null}
                 {index==4?<img  className="img" src={"https://www.wallpaperflare.com/static/29/673/262/tomato-lot-vegetable-food-wallpaper.jpg"}/>:null}
                 {index==5?<img  className="img" src={"https://cdn.mos.cms.futurecdn.net/42E9as7NaTaAi4A6JcuFwG.jpg"}/>:null}
 

                 {/* <Card.Img variant="top" 
                 src={"https://www.wlns.com/wp-content/uploads/sites/50/2020/10/vegetables.jpg" } /> */}
                 <Card.Body>
                   <Card.Title>Name:{veg.product_name}</Card.Title>
                   <Card.Text>Price in Rs: {veg.product_price*veg.product_quantity} </Card.Text>
                   <Card.Text>Quantity: {veg.product_quantity} </Card.Text>
                   
                   <Button type="button" variant="danger" 
                   onClick={()=>this.props.delete(veg.product_id)}>
                   DELETE</Button>
                   </Card.Body>
                  </Card>
                 </ListGroup.Item>
            )}
            </ListGroup>
            <h1>Total Price in Rs :{this.state.total}</h1>
            <Link to='/profile'>
            <Button type="button" variant="secondary">
                   Place Order</Button>
            </Link>
           
            
      </div>
    )
  }
}
function mapStateToProps(state){
        return {
           products: state.VegetableReducer.productList,
           gtotal: state.totalReducer.total
        }
}
const mapDispatchToProps = dispatch => {
  return {
    delete: (product_id) => dispatch(deleteproduct(product_id)),
    gtotal: (total) => dispatch(gettotal(total))
  }
}
    
export default connect(mapStateToProps,mapDispatchToProps)(Cart);